<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <el-form-item label="" prop="projectName">
            <!-- 任务名称 start -->
            <el-input
              class="input-name"
              v-model="searchValidateForm.projectName"
              placeholder="项目名称"
            ></el-input>
            <!-- 任务名称 end -->
          </el-form-item>

          <!-- <el-form-item label="" prop="projectTask">

            <el-input
              class="input-name"
              v-model="searchValidateForm.projectTask"
              placeholder="项目任务"
            ></el-input>

          </el-form-item>

          <el-form-item label="" value="" prop="priority">
            <el-select
              v-model="searchValidateForm.priority"
              placeholder="请选择优先级"
            >
              <el-option
                v-for="item in priorityList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item> -->
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 查询数据 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 查询数据 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 新增 start -->
        <el-button
          type="primary"
          icon="el-icon-circle-plus-outline"
          @click="onAdd"
          >新增项目</el-button
        >
        <!-- 新增 end -->
      </el-header>

      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="this.tableData"
          style="width: 100%"
          border
          v-loading="tableLoading"
          @sort-change="sortChange"
          :default-sort="{ prop: 'deadline', order: 'descending' }"
        >
          <el-table-column label="项目名称" min-width="130" fixed="left">
            <template slot-scope="scope">
              <div>
                {{ scope.row.projectName }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="项目任务" min-width="130">
            <template slot-scope="scope">
              <div>
                {{ scope.row.projectTask }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="优先级" min-width="130">
            <template slot-scope="scope">
              <div v-if="scope.row.priority == 1">
                <!-- {{scope.row.priority}} -->
                <el-tag type="danger">{{
                  GetPriority(scope.row.priority)
                }}</el-tag>
              </div>
              <div v-if="scope.row.priority == 2">
                <!-- {{scope.row.priority}} -->
                <el-tag type="success">{{
                  GetPriority(scope.row.priority)
                }}</el-tag>
              </div>
              <div v-if="scope.row.priority == 3">
                <!-- {{scope.row.priority}} -->
                <el-tag type="info">{{
                  GetPriority(scope.row.priority)
                }}</el-tag>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="计划开始时间" min-width="130">
            <template slot-scope="scope">
              <div>
                {{ scope.row.startUpTime }}
              </div>
            </template>
          </el-table-column>
          <el-table-column
            label="计划结束时间"
            min-width="130"
            sortable
            :sort-orders="['ascending', 'descending']"
          >
            <template slot-scope="scope">
              <div>
                {{ scope.row.completionTime }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="天数" min-width="130">
            <template slot-scope="scope">
              <div>
                {{ scope.row.dayOfAlone }}
              </div>
            </template>
          </el-table-column>

          <el-table-column label="项目负责人" min-width="130">
            <template slot-scope="scope">
              <div>
                {{ scope.row.adminUserName }}
              </div>
            </template>
          </el-table-column>

          <el-table-column label="项目级别" min-width="130">
            <template slot-scope="scope">
              <div v-if="scope.row.level == 1">
                <!-- {{scope.row.priority}} -->
                <el-tag type="danger">{{
                  GetPriority(scope.row.level)
                }}</el-tag>
              </div>
              <div v-if="scope.row.level == 2">
                <!-- {{scope.row.priority}} -->
                <el-tag type="success">{{
                  GetPriority(scope.row.level)
                }}</el-tag>
              </div>
              <div v-if="scope.row.level == 3">
                <!-- {{scope.row.priority}} -->
                <el-tag type="info">{{ GetPriority(scope.row.level) }}</el-tag>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="是否可以并行" min-width="130">
            <template slot-scope="scope">
              <div>
                {{ scope.row.isParallel }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="130">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="editRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                编辑
              </el-button>
              <el-button
                @click.native.prevent="deleteRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->
      </el-main>

      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            @size-change="onSizeChange"
            @current-change="onCurrentChange"
            :current-page.sync="currentPage"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- dialog对话框 start -->
      <!-- 新增 dialog start -->
      <el-dialog
        class="dialog"
        width="50%"
        :title="dialogTitle"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
        :close-on-click-modal="false"
      >
        <!-- form表单 -->
        <el-form
          :model="validateForm"
          ref="validateForm"
          label-width="80px"
          class="demo-ruleForm"
          :rules="validateFormRules"
        >
          <!-- 项目名称 start -->
          <el-form-item label="项目名称" prop="projectName">
            <el-input
              v-model="validateForm.projectName"
              placeholder="请输入项目名称"
            ></el-input>
          </el-form-item>
          <!-- 项目名称 end -->

          <!-- 项目任务 start -->
          <el-form-item label="项目任务" prop="projectTask">
            <el-input
              v-model="validateForm.projectTask"
              placeholder="请输入项目任务"
            ></el-input>
          </el-form-item>
          <!-- 项目任务 end -->

          <!-- 优先级 start -->
          <el-form-item label="优先级" prop="priority">
            <el-select
              v-model="validateForm.priority"
              placeholder="请选择优先级"
            >
              <el-option
                v-for="item in priorityList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 优先级 end -->

          <!-- 时间节点 -->
          <el-form-item label="时间节点" prop="timenode">
            <el-date-picker
              type="daterange"
              v-model="timenode"
              start-placeholder="开始日期"
              range-separator="至"
              end-placeholder="结束日期"
              @change="timeChange"
            >
            </el-date-picker>
          </el-form-item>
          <!-- 时间节点 -->

          <!-- 天数 start -->
          <el-form-item label="天数" prop="dayOfAlone">
            <el-input
              v-model="validateForm.dayOfAlone"
              placeholder="请输入天数"
            ></el-input>
          </el-form-item>
          <!-- 天数 end -->

          <!-- 项目负责人 start -->
          <el-form-item label="负责人" prop="adminUserName">
            <el-select
              v-model="selectedadminUserName"
              placeholder="请选择项目负责人"
              @change="handleadminUserNameChange"
            >
              <el-option
                v-for="item in adminUserNamelist"
                :key="item.value"
                :label="item.label"
                :value="item.memberName"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 项目负责人 end -->

          <!-- 项目级别 start -->
          <el-form-item label="项目级别" prop="level">
            <el-select
              v-model="validateForm.level"
              placeholder="请选择项目级别"
            >
              <el-option
                v-for="item in levelList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 项目级别 end -->

          <!-- 是否可以并行 start -->
          <el-form-item label="是否并行" prop="isParallel">
            <el-select
              v-model="validateForm.isParallel"
              placeholder="请选择是否可以并行"
            >
              <el-option
                v-for="item in isParallellist"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 是否可以并行 end -->

          <!-- 备注 start -->
          <!-- <el-form-item label="备注" prop="remarks">
            <el-input
              v-model="validateForm.remarks"
              placeholder="备注"
            ></el-input>
          </el-form-item> -->
          <!-- 备注 end -->
        </el-form>
        <!-- form表单 -->

        <!-- 提交 start -->
        <div class="submit">
          <el-button type="primary" @click="submitForm('validateForm')"
            >提交</el-button
          >
          <el-button @click="dialogClose">取消</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>

      <!-- 详情 dialog start -->
      <el-dialog
        class="dialog"
        title=""
        :visible.sync="detailDialogVisible"
        :before-close="detailHandleClose"
        :close-on-click-modal="false"
      >
        <el-descriptions title="工厂发货详情信息" border>
          <el-descriptions-item
            v-for="(item, index) in detailTableList"
            :key="index"
            :label="index"
            >{{ item }}</el-descriptions-item
          >
        </el-descriptions>
      </el-dialog>
      <!-- 详情 dialog end -->
      <!-- dialog对话框 end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from "file-saver";
import XLSX from "xlsx";

import { formatMonth, createUrl, formatDate } from "../utils/utils.js";
export default {
  name: "softwareList",
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback();
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + "必须为数字值"));
        } else {
          callback();
        }
      };
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + "不能为空"));
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + "必须为数字值"));
        } else {
          callback();
        }
      };
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + "不能为空"));
        } else {
          callback();
        }
      };
    }

    return {
      approveDialogVisible: false,
      dialogTitle: "",
      appRoverData: [], //审批人
      showTable: true, // 是否显示表格
      tableData: [], // 表格数据
      tableLoading: false,

      searchCompanyOptions: [], // 搜索的公司选项
      searchScenicOptions: [], // 搜索的景区选项

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dialogVisible: false, // 是否显示添加dialog对话框
      editDialogVisible: false, // 是否显示编辑dialog对话框
      detailDialogVisible: false, // 是否显示详情dialog对话框

      detailTableList: [], // 详情列表

      dialogState: "", // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      selectDateState: "1", // 日期统计方式（月，日，年）
      dateType: "daterange", // 日期类型（月，日，年）
      valueFormat: "yyyy-MM-dd", // 日期格式

      searchValidateForm: {
        id: "",
        projectName: "", //项目名称
        projectTask: "", //项目任务
        priority: "", //优先级
        startUpTime: "", //计划开始时间
        completionTime: "", //计划结束时间
        dayOfAlone: "", //天数
        adminUserId: "", //项目负责人id
        adminUserName: "", //项目负责人姓名
        level: "", //项目级别
        isParallel: "", //是否并行
      }, // 搜索表单验证对象
      searchVF: {
        robotCode: "", // 机器人编号
        spotId: "", // 景区id
        faultName: "", // 故障名称
        faultTimeType: "", // 故障时间类型
        startEndDate: "", // 起止日期
        warrantyState: "", // 质保状态
        faultState: "", // 故障状态
        signState: "", // 配件签收状态
      }, // 搜索需要提交的表单
      validateForm: {
        id: "",
        projectName: "", //项目名称
        projectTask: "", //项目任务
        priority: "", //优先级
        startUpTime: "", //计划开始时间
        completionTime: "", //计划结束时间
        dayOfAlone: "", //天数
        adminUserId: "", //项目负责人id
        adminUserName: "", //项目负责人姓名
        level: "", //项目级别
        isParallel: "", //是否并行
      }, // 表单验证对象
      editValidateForm: {}, // 表单验证对象

      searchPartnerCompanyName: "", // 搜索的公司
      searchScenicName: "", // 搜索的景区
      searchSelectDateState: "1", // 搜索的日期统计方式
      searchStartEndDate: "", // 搜索的起止时间
      searchPayState: "", // 搜索的支付状态

      companys: [], // 公司列表
      companyState: "", // 当前请选择公司

      scenics: [], // 景区列表
      scenicState: "", // 当前请选择景区

      downloadUrl: "",

      total: 0,

      currentEditRowId: null, // 当前编辑的行id

      uploadDialogImageUrl: "",
      uploadDialogVisible: false,
      uploadDisabled: false, // 是否隐藏下载和删除按钮

      limitUpload: 1,
      file: null, // 上传的Excel文件
      fileTemp: null, // 上传的临时Excel文件
      fileListUpload: [], // 上传Excel文件列表
      modelScenicSpotName: [],

      // 搜索表单规则
      searchValidateFormRules: {},

      // 提交表单规则
      validateFormRules: {
        projectName: [
          {
            required: true,
            message: "请输入项目名称",
            trigger: "blur",
          },
        ],
        projectTask: [
          {
            required: true,
            message: "请输入项目任务",
            trigger: "blur",
          },
        ],
        priority: [
          {
            required: true,
            message: "请输入优先级",
            trigger: "blur",
          },
        ],
        timenode: [
          {
            required: true,
            message: "请输入时间节点",
            trigger: "change",
          },
        ],
        dayOfAlone: [
          {
            required: true,
            message: "请输入天数",
            trigger: "blur",
          },
        ],
        adminUserName: [
          {
            required: true,
            message: "请输入项目负责人",
            trigger: "blur",
          },
        ],
        level: [
          {
            required: true,
            message: "请输入项目级别",
            trigger: "blur",
          },
        ],
      },

      // 提交编辑表单规则
      editValidateFormRules: {},
      editDataRow: {},
      //优先级
      priorityList: [
        {
          value: "1",
          label: "A",
        },
        {
          value: "2",
          label: "B",
        },
        {
          value: "3",
          label: "C",
        },
      ],
      levelList: [
        {
          value: "1",
          label: "A",
        },
        {
          value: "2",
          label: "B",
        },
        {
          value: "3",
          label: "C",
        },
      ],
      isParallellist: [
        {
          value: "否",
          label: "false",
        },
        {
          value: "是",
          label: "true",
        },
      ],
      adminUserNamelist: [],
      selectedadminUserName: "", // 存储选中的项目负责人
      correspondingIDSSS: null, // 存储负责人对应的ID
      // getday: false,
      // days: "",
      // startUpTime: "",
      // completionTime: "",
      // timenode: [null, null],
      initTimeModle: new Date(),
      projectstattime: [],
      timenode: ["", ""], //因为对话框界面的时间节点都是受timechange变化而变化的，所以如果想要编辑界面的时间显示之前的数据就必须要新建一个数组，这个数组重新把表单里的数据拿到
    };
  },
  created() {
    //获取表格数据
    this.initTableData();
  },
  beforeMount() {},
  mounted() {},
  methods: {
    // 登录(必须)
    // async login() {
    //   const { data: res } = await this.$http.post(
    //     "/loginSystem?username=houjingchen&password=houjingchen"
    //   );
    // },
    // async touristsGetToken() {
    //   const { data: res } = await this.$http.get(
    //     "https://load.topsroboteer.com/system/fourrobot/queryRobotDataList.do?scenicSpotId=72809461224343",
    //     {
    //       params: {},
    //     }
    //   );
    // },

    /**
     * 初始化表格数据(这个版本不会报错)
     */
    async initTableData() {
      const { data: res } = await this.$http.get(
        "/projectManagement/project/pageAsc",
        {
          params: {
            projectName: this.searchValidateForm.projectName,
            projectTask: this.searchValidateForm.projectTask,
            priority: this.searchValidateForm.priority,
            pageNum: this.currentPage, // 页码
            currentPage: this.currentPage,
            pageSize: this.pageSize, // 每页条数
          },
        }
      );
      this.tableData = res.data;
      this.total = res.totals;
      this.adminUserNamelistData();
    },

    //调用负责人接口
    async adminUserNamelistData() {
      const { data: res } = await this.$http.get(
        "/projectManagement/userManage/groupMember/page",
        {
          params: {
            pageNum: this.currentPage, // 页码
            currentPage: this.currentPage,
            pageSize: this.pageSize, // 每页条数
          },
        }
      );
      this.adminUserNamelist = res.data;
    },
    async handleadminUserNameChange() {
      this.validateForm.adminUserName = this.selectedadminUserName;
      this.correspondingIDSSS = await this.findgroupLeaderID(
        this.selectedadminUserName
      );
      this.validateForm.adminUserId = this.correspondingIDSSS;
    },
    async findgroupLeaderID(memberName) {
      const index = this.adminUserNamelist.findIndex(
        (item) => item.memberName === memberName
      );
      if (index !== -1) {
        return this.adminUserNamelist[index].id;
      } else {
        return null;
      }
    },

    /**
     * 新增表格数据
     */
    async addTableData() {
      console.log(this.validateForm);
      const { data: res } = await this.$http({
        method: "post",
        url: "/projectManagement/project/save",
        data: this.validateForm,
        dataType: "json",
      });
      console.log(res);
      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: "success",
        });
        this.initTableData();
        // this.validateForm = {}; // 重置表单
      } else {
        this.$message({
          message: res.msg,
          type: "error",
        });
      }
    },

    /**
     * 修改表格数据
     */
    async editTableData() {
      const { data: res } = await this.$http({
        method: "post",
        url: "/projectManagement/project/edit",
        data: this.validateForm,
        dataType: "json",
      });
      console.log(res);
      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: "success",
        });
        this.initTableData();
      } else {
        this.$message({
          message: res.msg,
          type: "error",
        });
      }
    },
    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData() {
      var spotId = this.searchVF.spotId;
      if (spotId == "" || !spotId) {
        spotId = null;
      }

      var startEndDate = this.searchVF.startEndDate;
      var beginTime, endTime;
      if (startEndDate == "" || !startEndDate) {
        // beginDate = formatMonth()
        // endDate = formatMonth()
        beginTime = null;
        endTime = null;
      } else {
        beginTime = this.searchVF.startEndDate[0];
        endTime = this.searchVF.startEndDate[1];
      }

      var type = this.searchVF.type;
      if (type == "" || !type) {
        type = null;
      }

      const { data: res } = await this.$http.get(
        "/system/factory_send/sendList",
        {
          params: {
            spotId: spotId,
            beginDate: beginTime,
            endDate: endTime,
            type: type,
          },
        }
      );
      if (res.state == "200") {
        this.exportTableData = res.data;
      } else {
        return;
      }
    },
    //删除审批人
    delectAuditor(item, index) {
      console.log(item.appFlowPathDetailsList);
      this.auditorList.splice(index, 1);
      this.editDataRow.appFlowPathDetailsList.splice(index, 1);
    },
    //删除抄送人
    delectCopyAndSend(item, index) {
      console.log(item);
      item.appFlowPathDetailsList.splice(index, 1);
    },
    //添加抄送人
    onAddCopySend(item, index) {
      item.appFlowPathDetailsList.push({
        sort: index + 1,
        sysAppUserId: "",
        type: 2,
      });
      console.log(item);
      // this.auditorList
    },

    /**
     * 查询公司名称获取列表数据
     */
    getTableData() {
      // this.searchVF.spotId = this.searchValidateForm.SCENIC_SPOT_ID

      // 否则调用接口，请求数据，为tableData重新赋值
      console.log("调用接口");
      this.initTableData();
    },

    // 景区选项变化时触发
    async onChangeSpot(spotId) {
      var spotName = "";
      this.searchScenicOptions.forEach((item) => {
        if (item.spotId == spotId) {
          spotName = item.spotName;
        }
      });
      this.validateForm.spotName = spotName;
    },

    // 日期选项值发生变化时触发
    onDateChange() {
      console.log(this.selectDateState);
      if (this.selectDateState == "1") {
        // 按月查询
        this.dateType = "monthrange";
        this.valueFormat = "yyyy-MM";
      } else if (this.selectDateState == "2") {
        // 按日查询
        this.dateType = "daterange";
        this.valueFormat = "yyyy-MM-dd";
      } else if (this.selectDateState == "3") {
        // 按年查询
        this.dateType = "yearrange";
        this.valueFormat = "yyyy";
      }
    },

    // 重置
    onReset() {
      // 清空所有输入框内容
      this.searchValidateForm.projectName = "";
      this.searchValidateForm.projectTask = "";
      this.searchValidateForm.priority = "";
      // 重新获取搜索景区列表
      this.initTableData();
      // this.touristsGetToken();
    },

    // 点击查询数据按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.showTable = true;
          this.getTableData();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    // 导出Excel表格
    exportExcel() {
      this.$confirm("确认导出Excel表格?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(async () => {
          this.downloadLoading = true;
          await this.getExportTableData();
          // 使用nextTick当table表格渲染完成后执行导出表格操作
          this.$nextTick(() => {
            var xlsxParam = { raw: true }; //转换成excel时，使用原始的格式
            /* 从表生成工作簿对象 */
            var wb = XLSX.utils.table_to_book(
              document.querySelector("#outTable"),
              xlsxParam
            );
            /* 获取二进制字符串作为输出 */
            var wbout = XLSX.write(wb, {
              bookType: "xlsx",
              bookSST: true,
              type: "array",
            });
            try {
              FileSaver.saveAs(
                //Blob 对象表示一个不可变、原始数据的类文件对象。
                //Blob 表示的不一定是JavaScript原生格式的数据。
                //File 接口基于Blob，继承了 blob 的功能并将其扩展使其支持用户系统上的文件。
                //返回一个新创建的 Blob 对象，其内容由参数中给定的数组串联组成。
                new Blob([wbout], {
                  type: "application/octet-stream",
                }),
                //设置导出文件名称
                "订单统计状态" + ".xlsx"
              );
            } catch (e) {
              if (typeof console !== "undefined") console.log(e, wbout);
            }
            this.downloadLoading = false;
            return wbout;
          });

          this.$message({
            type: "success",
            message: "已成功导出Excel表格!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消导出Excel表格",
          });
        });
    },

    // 新增
    onAdd() {
      this.dialogVisible = true;
      this.dialogState = "add";
      this.dialogTitle = "新增任务信息";
      this.validateForm = {
        timenode: ["", ""],
      };
      this.timenode = [];
      this.selectedadminUserName = "";
    },

    // 点击编辑时触发
    editRow(index, row) {
      this.dialogVisible = true; // 是否显示编辑dialog对话框
      // 显示 dialog 弹窗
      this.dialogState = "edit";
      var rowObj = Object.assign({}, row); // 深拷贝row对象
      this.validateForm = rowObj;
      // var timenode = ["", ""];
      this.timenode[0] = this.validateForm.startUpTime;
      this.timenode[1] = this.validateForm.completionTime;
      this.validateForm.timenode = ["", ""];
      this.selectedadminUserName = this.validateForm.adminUserName;
      this.dialogTitle = "编辑任务信息";
    },

    //点击删除触发
    deleteRow(index, row) {
      this.$confirm("此操作将删除该行数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(async () => {
          this.deleteTableDate(row.id);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
            offset: 100,
            center: true,
            duration: 1000,
          });
        });
    },
    async deleteTableDate(id) {
      const { data: res } = await this.$http({
        method: "post",
        url: "/projectManagement/project/remove",
        data: { id: id },
      });

      // console.log(res);
      if (res.state == "200") {
        this.$message({
          type: "success",
          message: "删除成功",
          offset: 100,
          center: true,
          duration: 1000,
        });
        this.initTableData();
      } else {
        this.$message({
          type: "error",
          message: res.msg,
          offset: 100,
          center: true,
          duration: 3000,
        });
      }
    },

    // 新增 dialog 取消
    dialogClose() {
      this.dialogVisible = false;
      this.$refs.validateForm.resetFields(); // 重置表单（清空无效）
      // this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 编辑 dialog 取消
    editDialogClose() {
      this.editDialogVisible = false;
      this.$refs.editValidateForm.resetFields(); // 重置表单（清空无效）
      this.editValidateForm = {};
      // this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 详情 dialog 取消
    detailDialogClose() {
      this.detailDialogVisible = false;
    },

    // dialog 右上角关闭按钮
    handleClose(done) {
      done(); // 关闭dialog对话框
      this.$refs.validateForm.resetFields(); // 重置表单（清空无效）
      // this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 编辑 dialog 右上角关闭按钮
    editHandleClose(done) {
      done(); // 关闭dialog对话框
      this.$refs.editValidateForm.resetFields(); // 重置表单（清空无效）
      this.editValidateForm = {};
    },

    // 详情 dialog 右上角关闭按钮
    detailHandleClose(done) {
      done();
    },

    // 审批人关闭右上角关闭按钮
    approveHandleClose(done) {
      this.appRoverData = [];
      done();
    },

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.dialogState == "add") {
            // 新增任务信息
            console.log("新增任务信息");
            this.addTableData();
            this.dialogVisible = false;
          } else if (this.dialogState == "edit") {
            // 编辑任务信息
            console.log("编辑任务信息");
            this.editTableData();
            this.dialogVisible = false;
          } else if (this.dialogState == "detail") {
            // 任务详情信息
            console.log("任务详情信息");
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    onSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.initTableData();
    },
    onCurrentChange(currentPage) {
      this.currentPage = currentPage;
      this.initTableData();
    },

    // 文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
    handleChange(file, fileList) {
      console.log(file);
      this.fileTemp = file.raw;

      // 导入判断
      if (this.fileTemp) {
        if (
          this.fileTemp.type ==
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" ||
          this.fileTemp.type == "application/vnd.ms-excel"
        ) {
          this.importfxx(this.fileTemp);
        } else {
          this.$message({
            type: "warning",
            message: "附件格式错误，请删除后重新上传！",
          });
        }
      } else {
        this.$message({
          type: "warning",
          message: "请上传附件！",
        });
      }
    },

    // 文件超出个数限制时的钩子
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${
          files.length + fileList.length
        } 个文件`
      );
    },

    // 文件列表移除文件时的钩子
    handleRemove(file, fileList) {
      this.fileTemp = null;
    },

    // 导入格式
    importfxx(obj) {
      let _this = this;
      // 通过DOM取文件数据
      this.file = obj;
      var rABS = false; //是否将文件读取为二进制字符串
      var f = this.file;
      var reader = new FileReader();
      //if (!FileReader.prototype.readAsBinaryString) {
      FileReader.prototype.readAsBinaryString = function (f) {
        var binary = "";
        var rABS = false; //是否将文件读取为二进制字符串
        var pt = this;
        var wb; //读取完成的数据
        var outdata;
        var reader = new FileReader();
        reader.onload = function (e) {
          var bytes = new Uint8Array(reader.result);
          var length = bytes.byteLength;
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i]);
          }
          var XLSX = require("xlsx");
          if (rABS) {
            wb = XLSX.read(btoa(fixdata(binary)), {
              //手动转化
              type: "base64",
            });
          } else {
            wb = XLSX.read(binary, {
              type: "binary",
            });
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]); //outdata就是你想要的东西
          console.log(outdata);
          this.da = [...outdata];
          let arr = [];
          this.da.map((v) => {
            let obj = {
              robotCount: v["机器人数量"],
              type: v["发货进度"],
              applicantName: v["申请人"],
              factoryName: v["发货工厂"],
              consignorName: v["发货人"],
              receivingName: v["收货景区"],
              consigneeName: v["收货人"],
              consigneePhone: v["收货人电话"],
              receiningAddress: v["收货地址"],
              consignmentDate: v["发货时间"],
            };
            arr.push(obj);
          });
          // 将 arr 赋值给 tableData，来更新表格数据
          this.tableData = arr;
          return arr;
        };
        reader.readAsArrayBuffer(f);
      };

      if (rABS) {
        reader.readAsArrayBuffer(f);
      } else {
        reader.readAsBinaryString(f);
      }
    },

    //排序
    async sortChange(column, prop, order) {
      if (column.order == "descending") {
        const { data: res } = await this.$http.get(
          "/projectManagement/project/pageDesc",
          {
            params: {
              pageSize: this.pageSize,
              pageNum: this.currentPage,
              currentPage: this.currentPage,
            },
          }
        );
        if (res.code == "200") {
          this.tableData = res.data;
          this.total = res.totals;
          this.tableLoading = false;
          this.deadline = res.data.deadline;
        } else {
          this.$message({
            type: "error",
            message: res.msg,
          });
        }
      } else if (column.order == "ascending") {
        const { data: res } = await this.$http.get(
          "/projectManagement/project/pageAsc",
          {
            params: {
              pageSize: this.pageSize,
              pageNum: this.currentPage,
              currentPage: this.currentPage,
            },
          }
        );
        if (res.code == "200") {
          this.tableData = res.data;
          this.total = res.totals;
          this.tableLoading = false;
          this.deadline = res.data.deadline;
        } else {
          this.$message({
            type: "error",
            message: res.msg,
          });
        }
      } else {
        column.order = "descending";
      }
    },

    //时间获取
    timeChange(e) {
      var time = [];
      e.forEach((item, index) => {
        const date = new Date(item);

        const formattedDate =
          date.getFullYear() +
          "-" +
          (date.getMonth() + 1).toString().padStart(2, "0") +
          "-" +
          date.getDate().toString().padStart(2, "0");
        time.push(formattedDate);
      });
      this.validateForm.startUpTime = time[0];
      this.validateForm.completionTime = time[1];
    },
  },

  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
    GetPriority() {
      return (row) => {
        console.log(row);
        if (row == 1) {
          return "A";
        } else if (row == 2) {
          return "B";
        } else if (row == 3) {
          return "C";
        }
      };
    },
    SpotName() {
      return (row) => {
        var spotData = [];
        row.appFlowPathSpotList.forEach((item) => {
          spotData.push(item.scenicSpotName);
        });
        // console.log(spotData)
        return spotData + "";
        // console.log(row.appFlowPathSpotList)
      };
    },
  },
};
</script>

